  }

  to {
    -moz-transform: rotate(360deg) translateY(250px) rotate(-360deg);
  }
}

@-o-keyframes orbit4 {
  from {
    -o-transform: rotate(0deg) translateY(250px) rotate(0deg);
  }

  to {
    -o-transform: rotate(360deg) translateY(250px) rotate(-360deg);
